export default function Card(props) {
    return (
      <div className="card-container">
        <div className="card-header">
            <h1>{props.title}</h1>
        </div>
        <div className="card-content">
            {props.children}
        </div>

        <style jsx>
        {`
          .card-container {
            padding: 16px;
          	margin: 16px;

            box-shadow: 0 1px 4px 2px rgba(50, 50, 50, 0.1);
            border-radius: 2px;
          }

          .card-header {

          }

          .card-content {
            font-size: 1em;
          }
        `}
        </style>
      </div>
    )
  }